	// Round up numbers to 2 decimal places
	var roundUpDec = (value, decimals) => Number(Math.round(value+'e'+decimals)+'e-'+decimals);

	// Converts from degrees to percent.
	var degreeToPercent = (degrees) => (degrees / 360 * 100);

